	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","author":"Matthieu Oger"}},"markdownRemark":{"html":"<p><a href=\"http://www.quora.com/iOS-7/Is-the-new-Apple-iOS-7-look-an-improvement/answer/David-Cole\">David Cole on Quora</a>:</p>\n<blockquote>\n<p>I have a different perspective than most of the other answerers here. I don't think the interesting components of iOS 7 are found in the icons or the color or the type — those are relatively self-contained and thus easy to change and evolve. I think the most interesting changes, and the best changes, run much deeper than that, and yet they're intrinsically linked to the \"look\" of it.</p>\n</blockquote>\n<p>One of the best analysis that I've read about iOS 7.</p>","frontmatter":{"date":"2013-07-04","readableDate":"04 July 2013","title":"Is the new Apple iOS 7 look an improvement?","showPageTitle":null},"fields":{"path":"/2013/07/ios7-david-cole/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2013-07-04-ios7-david-cole.md absPath of file >>> MarkdownRemark"}}

